<template>
  <div>
    <vue-ace-editor
      ref="editor"
      :content="content"
      :fontSize="14"
      height="210px"
      lang="python"
      theme="eclipse"
      @init="editorInit"
      @onChange="editorChange"
      @onInput="editorInput"
      @onFocus="editorFocus"
      @onBlur="editorBlur"
      @onPaste="editorPaste"
    ></vue-ace-editor>
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  name: "home",
  data() {
    return {
      content: ''
    };
  },
  methods: {
    editorInit() {
      require("brace/ext/language_tools");
      require(`brace/mode/python`);
      require(`brace/snippets/python`);
      require(`brace/theme/eclipse`);
    },
    editorChange(editor) {
      this.editor = editor
      // console.log("changed", editor.getValue());
    },
    editorInput(editor) {
      // console.log("input", editor.getValue());
    },
    editorFocus(editor) {
      // console.log("focus", editor);
    },
    editorBlur(editor) {
      this.editor = editor
      window.parent.postMessage({
        code: editor.getValue()
      },'*');
      // console.log("blur", editor.getValue());
    },
    editorPaste(editor) {
      // console.log("pase", editor);
    }
  },
  mounted() {
    window.addEventListener('message', (e) => {
      if (e.data.clear) {
        this.editor.setValue('')
        this.content = ''
      }
      if (e.data.code) {
        this.content = e.data.code
        document.querySelector('.ace_text-input').onkeydown = (e) => {
          e.preventDefault()
        }
      }
    })
  }
};
</script>
